body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/** graph components **/
.header {
    margin-bottom: 0px;
    text-align: left;
}

.sorry {
    font-size: 12px;
    margin-top: 40vh;
    margin-left: auto;
    margin-right: auto;
    width: 90%;
    -webkit-align-items: center;
            align-items: center;
}

.key {
    -webkit-align-items: left;
            align-items: left;
    width: 20%
}

.mobileHeading {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.heading {
    margin-top: 4%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 80%;
    -webkit-align-items: center;
            align-items: center;
    position: relative;
}

.bigCanvas { 
    width: 80%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 1vh;
}

.smallerCanvas { 
    width: 100%;
    margin-top: 3vh;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

@media only screen and (max-width: 550px) {
    .heading {
        margin-top: 10%;
    }
}

@media only screen and (max-width: 400px) {
    .header {
        font-size: 16px;
    }

}


.songInfo {
    background-color: #000;
    color: #fff;
    background: rgb(204, 204, 204); /* Fallback for older browsers without RGBA-support */
    background: rgba(52, 51, 51, 0.5);   
    border: 1px solid grey;
    border-radius: 0.5em;
    width: 180px;
}

.smallSongInfo {
    background-color: #000;
    color: #fff;
    background: rgb(204, 204, 204); /* Fallback for older browsers without RGBA-support */
    background: rgba(52, 51, 51, 0.5);   
    border: 1px solid grey;
    border-radius: 0.5em;
    width: 100px;
}

.mobile {
    background-color: #000;
    color: #fff;
    background: rgb(204, 204, 204); /* Fallback for older browsers without RGBA-support */
    background: rgba(52, 51, 51, 0.5);   
    border: 1px solid grey;
    border-radius: 0.5em;
    margin: auto;
    width: 16rem;
    margin-top: 2vh;
}

.container {
    width: 100%;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    margin-bottom: 20px;
}

.name { 
    margin: 5px;
    text-align: left;
    font-size: 13px;
    margin-bottom: 0;
    margin-right: 10px;
}

.artist { 
    margin: 5px;
    margin-top: 0;
    text-align: left;
    font-size: xx-small; 
    margin-right: 10px;
    margin-bottom: 0;
}

.left {
    text-align: left;
    width: 48%;
    margin-right: 4%;
}

.right {
    text-align: left;
    width: 48%;
}

.list {
    display: -webkit-flex;
    display: flex;
}

.top10 {
    display: block;
    margin-left: 0;
    margin-right: auto;
    margin-top: 4vh;
    text-align: left;
    width: 50%;
    font-size: x-large;
} 

.learnMore { 
    display: block;
    font-weight: 900;
    margin-left: 0;
    margin-right: auto;
    margin-top: 6px;
    text-align: left;
    font-size: x-small;
    margin-bottom: 6px;
    color: #1DB954;
}

.loadingDiv { 
    height: 100px;
}

.loading {
    margin-top: 4vh;
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 80%;
    text-align: left;
    font-size: small;
}

.loadingTime {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 80%;
    text-align: left;
    font-size: x-small;
    margin-bottom: 10px;
    color: #1DB954;
    margin-top: 5px;
    font-weight: 900;
}

.topzz {
    display: block;
    margin-left: auto;
    margin-right: auto;
    -webkit-align-items: center;
            align-items: center;
    position: relative;
    width: 80%;
    margin-bottom: 20px;
}

.songCover {
    height: 50px;
    margin: 10px;
}

@media only screen and (max-width: 600px) {
    .left {
        font-size: 14px;
    }

    .right {
        font-size: 14px;
    }

    .top10 {
        font-size: 18px;
    }
}

@media only screen and (max-width: 400px) {
    .left {
        font-size: 3vw;
    }

    .right {
        font-size: 3vw;
    }

    .top10 {
        font-size: 4vw;
    }
}

.genreComponent {
    font-size: xx-small;
    background-color: #000;
    background: rgb(204, 204, 204); /* Fallback for older browsers without RGBA-support */
    background: rgba(52, 51, 51, 0.5);   
    border: 1px solid grey;
    border-radius: 0.5em;
    color: #fff;
    margin-left: 5px;
    padding-left: 5px;
    padding-right: 5px;
    margin-top: 2px;
}

.parent { 
    display: -webkit-flex; 
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    text-align: left;
    margin-right: 10px;
    max-width: 180px;
    margin-bottom: 5px;
}
.header {
    font-size: 24px;
    margin-bottom: 0;
}

.moreInfo {
    font-size: 12px;
    margin-top: 0;
    margin-bottom: 10px;
    text-align: left;
    color: #1DB954;
    font-weight: 900;
    margin-top: 10px;
}

.songs {
    font-size: 14px;
    text-align: left;
}

.top {
    border-left: 1px solid grey;
    padding-left: 10px;
    margin-left: 25px;
    margin-right: 35px;
    margin-top: 50px;
    margin-bottom: 50px;
    width: 180px;
}

.margins {
    background-color: #000;
    color: #fff;
    background: rgb(204, 204, 204); /* Fallback for older browsers without RGBA-support */
    background: rgba(52, 51, 51, 0.5);   
    border-radius: 0.5em;
    margin-left: 130px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin-top: 10%;
}

.load {
    font-size: 20px;
    padding-top: 20px;
    margin-left: 10px;
}

@media only screen and (max-width: 900px) {
    .margins {
        display: none;
    }
  }
.App {
  text-align: center;
}

.App-header {
  background-color: #000;
  width: 100%;
  overflow-x:hidden;
  display: block;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  color: white;
  margin-left: auto;
  margin-right: auto;
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}

html, body {
  height: 100%;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #000;
  color: #eee;
  font-family: aktiv-grotesk, sans-serif;  
  font-size: 3vmin;
  font-style: normal;
  font-weight: 200;
}

.hidden {
  display: none;
}

.intro {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

/** Buttons **/

.btn {
  background-color: rgba(52, 51, 51, 0.5);   
  border-radius: 2em;
  border: 1px solid grey;
  color: white;
  font-size: 14px;
  padding: 0.7em 1.5em;
  text-transform: uppercase;
  transition: all 0.25s ease;
  margin-top: 20px;
}

.btn:hover {
  background: #1DB954;
  border: none;
  color: #000;
}

.btn--login {
  margin: 0 auto;
}

/** Front Page **/

.logo {
  margin-top: 10px;
  width: 10%;
  height: 10%;
  padding-bottom: 130px;
}

.main {
  display: inline;
  margin-top: 30vh;
}

.hi {
  margin-top: 20px;
  font-size: 10px;
  color: grey;
}

.title {
  font-size: 60px;
}

@media only screen and (max-width: 500px) {
  .logo {
    margin-top: 10px;
    width: 20%;
    height: 20%;
  }

}

@media only screen and (max-width: 400px) {
  .title {
    font-size: 40px;
  }

  .btn {
    font-size: 10px;
  }

  .logo {
    margin-top: 10px;
    width: 25%;
    height: 25%;
  }

  .hi {
    width: 150px; 
    margin-left: auto;
    margin-right: auto;
  }
}

@media only screen and (max-height: 500px) {
  .main {
    margin-top: 12vh;
  }
}
